import { defineComponent } from 'vue';
export default defineComponent({
    props: {
        dataList: {
            type: Array,
            require: true
        },
        propList: {
            type: Array,
            require: true
        },
        showSelectColumn: {
            type: Boolean,
            default: false
        },
        showIndexColumn: {
            type: Boolean,
            default: false
        },
        title: {
            type: String
        },
        page: {
            type: Object,
            default: () => ({})
        },
        listCount: {
            type: Number
        },
        showFooter: {
            type: Boolean,
            default: true
        },
        childrenProps: {
            type: Object,
            default: () => ({})
        }
    },
    emits: ['update:page'],
    setup(prop, { emit }) {
        // 最左侧的选择框
        const handleSelectionChange = (value) => {
            console.log(value);
        };
        // 底部分页器事件
        const handleSizeChange = (pageSize) => {
            emit('update:page', { ...prop.page, pageSize });
        };
        const handleCurrentChange = (currentPage) => {
            emit('update:page', { ...prop.page, currentPage });
        };
        return { handleSelectionChange, handleSizeChange, handleCurrentChange };
    }
});
