import { defineComponent } from 'vue';
export default defineComponent({
    props: {
        modelValue: {
            type: Object,
            require: true
        },
        formItems: {
            type: Array,
            default: () => []
        },
        labelWidth: {
            type: Number,
            default: 100
        },
        itemStyle: {
            type: Object,
            default: () => ({ padding: '10px 40px' })
        },
        colLayout: {
            type: Object,
            // 默认是响应式
            default: () => ({
                xl: 6,
                lg: 8,
                md: 12,
                sm: 24,
                xs: 24
            })
        }
    },
    emits: ['update:modelValue'],
    setup(prop, { emit }) {
        const handelModel = (value, field) => {
            emit(`update:modelValue`, { ...prop.modelValue, [field]: value });
        };
        return { handelModel };
    }
});
