import { defineComponent } from 'vue';
import HdTable from '@/base-ui/table';
import { useStore } from 'vuex';
import { computed, ref, watch } from 'vue';
import { usePermission } from '@/hooks/use-permission';
export default defineComponent({
    components: {
        HdTable
    },
    props: {
        contnetTableConfig: {
            type: Object,
            required: true
        },
        pageName: {
            type: String,
            required: true
        }
    },
    emits: ['updateBtnClick', 'createBtnClick'],
    setup(prop, { emit }) {
        const store = useStore();
        // 0获取按钮权限
        const isCreate = usePermission(prop.pageName, 'create');
        const isUpdate = usePermission(prop.pageName, 'update');
        const isDelete = usePermission(prop.pageName, 'delete');
        const isQuery = usePermission(prop.pageName, 'query');
        // console.log(prop.pageName)
        // 1.
        const pageInfo = ref({
            currentPage: 1,
            pageSize: 10
        });
        watch(pageInfo, () => {
            getPageData();
        });
        // 2.
        const getPageData = (queryInfo = {}) => {
            if (!isQuery)
                return;
            store.dispatch('system/getPageListAction', {
                pageName: prop.pageName,
                queryInfo: {
                    offset: (pageInfo.value.currentPage - 1) * pageInfo.value.pageSize,
                    size: pageInfo.value.pageSize,
                    ...queryInfo
                }
            });
        };
        getPageData();
        // 3.
        const dataCount = computed(() => {
            return store.getters['system/pageListCount'](prop.pageName);
        });
        const dataList = computed(() => store.getters[`system/pageListData`](prop.pageName));
        // 4. 动态插槽
        const OtherSlotNames = prop.contnetTableConfig?.propList.filter((item) => {
            if (item.slotName === 'enable')
                return false;
            if (item.slotName === 'createAt')
                return false;
            if (item.slotName === 'updateAt')
                return false;
            if (item.slotName === 'handler')
                return false;
            return true;
        });
        // 5.
        const handleDelete = async (item) => {
            console.log(item);
            // 删除该项
            store.dispatch('system/deletePageDataAction', {
                pageName: prop.pageName,
                id: item.id
            });
            // 获取删除后的表格数据
            getPageData();
        };
        const handleUpdate = (item) => {
            // console.log(22)
            emit('updateBtnClick', item);
        };
        const handleCreate = () => {
            emit('createBtnClick');
        };
        return {
            dataList,
            getPageData,
            pageInfo,
            dataCount,
            OtherSlotNames,
            isCreate,
            isUpdate,
            isDelete,
            handleDelete,
            handleUpdate,
            handleCreate
        };
    }
});
